___CSS_LOADER_EXPORT___.push([module.id, `/* Sidebar Styling */
.side-bar {
    overflow: scroll; 
    margin: 4rem;
    padding-top: 30px;
    background-color: var(--sidebar-bg); /* Soft pastel lavender-gray */
    border-radius: 15px; /* Rounded corners for the sidebar */
    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.7); /* Subtle shadow */
    max-height: 90vh;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE and Edge */
}

.side-bar::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera */
}

.time-based, .project-based{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    width: 100%;
}

.time-based>li, .project-based>li{
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    gap: 0.8rem;
    width: 100%;
    border-bottom: 1px solid var(--primary-dark);
}

/* Active state for sidebar items */
.active {
    background-color: var(--primary-dark); /* Darker violet for active items */
    font-weight: bold;
    color: var(--background-color); /* Light text color for contrast */
    padding: 5px 10px;
    transition: background-color 0.3s ease, transform 0.2s ease;
    width: 100%;
}

/* Task name styling */
.task-name, .task-count {
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    color: var(--text-color);
    transition: background-color 0.3s ease, color 0.3s ease;
}

.new-task{
    height: 3rem;
    font-size: 2rem;
    font-weight: bold;
    border-radius: 0px;
}

.time-based> *:hover {
    color: var(--primary-dark); /* Change color on hover for better UX */
    background-color: var(--primary-dark);
}

/* Project item styling */
.project-items,
.project-item {
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    color: var(--text-color);
    transition: background-color 0.3s ease, color 0.3s ease;
}

/* Active state for project items */
.project-item .active {
    background-color: var(--primary-dark); /* Darker violet for active items */
    font-weight: bold;
    color: var(--background-color); /* Light text color for contrast */
    padding: 5px 10px;
    transition: background-color 0.3s ease, transform 0.2s ease;
    width: 100%;
}

/* Hover effect for project items */
.project-item:hover {
    background-color: var(--primary-dark);
}

/* Optional: Add hover effect to active items */


.side-bar> ul{
    list-style:none;
}`, "",{"version":3,"sources":["webpack://./src/style/sidebar.css"],"names":[],"mappings":"AAAA,oBAAoB;AACpB;IACI,gBAAgB;IAChB,YAAY;IACZ,iBAAiB;IACjB,mCAAmC,EAAE,8BAA8B;IACnE,mBAAmB,EAAE,oCAAoC;IACzD,wCAAwC,EAAE,kBAAkB;IAC5D,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,qBAAqB,EAAE,YAAY;IACnC,wBAAwB,EAAE,gBAAgB;AAC9C;;AAEA;IACI,aAAa,EAAE,0BAA0B;AAC7C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,WAAW;AACf;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,kBAAkB;IAClB,WAAW;IACX,WAAW;IACX,4CAA4C;AAChD;;AAEA,mCAAmC;AACnC;IACI,qCAAqC,EAAE,mCAAmC;IAC1E,iBAAiB;IACjB,8BAA8B,EAAE,kCAAkC;IAClE,iBAAiB;IACjB,2DAA2D;IAC3D,WAAW;AACf;;AAEA,sBAAsB;AACtB;IACI,eAAe;IACf,iBAAiB;IACjB,eAAe;IACf,wBAAwB;IACxB,uDAAuD;AAC3D;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,0BAA0B,EAAE,wCAAwC;IACpE,qCAAqC;AACzC;;AAEA,yBAAyB;AACzB;;IAEI,eAAe;IACf,iBAAiB;IACjB,eAAe;IACf,wBAAwB;IACxB,uDAAuD;AAC3D;;AAEA,mCAAmC;AACnC;IACI,qCAAqC,EAAE,mCAAmC;IAC1E,iBAAiB;IACjB,8BAA8B,EAAE,kCAAkC;IAClE,iBAAiB;IACjB,2DAA2D;IAC3D,WAAW;AACf;;AAEA,mCAAmC;AACnC;IACI,qCAAqC;AACzC;;AAEA,+CAA+C;;;AAG/C;IACI,eAAe;AACnB","sourcesContent":["/* Sidebar Styling */\n.side-bar {\n    overflow: scroll; \n    margin: 4rem;\n    padding-top: 30px;\n    background-color: var(--sidebar-bg); /* Soft pastel lavender-gray */\n    border-radius: 15px; /* Rounded corners for the sidebar */\n    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.7); /* Subtle shadow */\n    max-height: 90vh;\n    display: flex;\n    flex-direction: column;\n    gap: 2rem;\n    scrollbar-width: none; /* Firefox */\n    -ms-overflow-style: none; /* IE and Edge */\n}\n\n.side-bar::-webkit-scrollbar {\n    display: none; /* Chrome, Safari, Opera */\n}\n\n.time-based, .project-based{\n    display: flex;\n    flex-direction: column;\n    gap: 0.8rem;\n    width: 100%;\n}\n\n.time-based>li, .project-based>li{\n    display: flex;\n    justify-content: space-between;\n    padding: 10px 20px;\n    gap: 0.8rem;\n    width: 100%;\n    border-bottom: 1px solid var(--primary-dark);