                  0 0 0 -2px #4F29F0, 
                  0 0 0 -2px #4F29F0,
                  0 0 0 -2px #4F29F0,
                  0 0 0 -2px #4F29F0,
                  0 0 0 -2px #4F29F0;
    }
    30% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      box-shadow: 0 -15px 0 0px #4F29F0,
                  14px -8px 0 0px #4F29F0,
                  14px 8px 0 0px #4F29F0,
                  0 15px 0 0px #4F29F0,
                  -14px 8px 0 0px #4F29F0,
                  -14px -8px 0 0px #4F29F0;
    }
  }
  @keyframes firework-11 {
    0% {
      opacity: 1;
      box-shadow: 0 0 0 -2px #4F29F0,
