                  0 15px 0 0px #4F29F0, 
                  -14px 8px 0 0px #4F29F0, 
                  -14px -8px 0 0px #4F29F0;
    }
  }

  /* Container for text elements */
.text-wrapper {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
  
  .done-wrapper input[type=checkbox]:checked ~ .text-wrapper>* {
    color: var(--disabled); /* Applies the color to all text elements inside the text-wrapper */
