___CSS_LOADER_EXPORT___.push([module.id, `/* General modal styles */
.modal {
  display: none; /* Hidden by default */
  position: fixed; 
  z-index: 1000; 
  left: 0;
  top: 0;
  width: 100%;
  height: 100%; 
  overflow: auto; 
  background-color: rgba(51, 110, 199, 0.5); 
}

/* Modal content styles */
.modal-content {
  background-color: #ffffff; 
  margin: 10% auto; 
  padding: 20px;
  border-radius: 10px; 
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); 
  max-width: 500px; /* Max width of modal */
}

/* Close button styles */
.span-close {
  color: #aaa; 
  float: right; 
  font-size: 28px; 
  font-weight: bold; 
}

.span-close:hover,
.span-close:focus {
  color: black; 
  text-decoration: none; 
  cursor: pointer; 
}

/* Modal form styles */
.modal-form {
  margin-top: 20px; 
}

/* Input field styles */
.modal-input-field {
  margin-bottom: 15px; 
}

.modal-form-input {
  width: calc(100% - 20px); 
  padding: 10px; 
  border: 1px solid #ccc; 
  border-radius: 5px;
  font-size: 16px; 
}

/* Priority slider styles */
.priority-slider {
  display: flex; 
  align-items: center; 
}

#priority-slider {
  margin-right: 10px;
}

/* Submit button styles */
.submit-modal-btn {
  background-color: #4CAF50; 
  color: white; 
  padding: 10px 15px; 
  border: none; 
  border-radius: 5px; 
  cursor: pointer; 
  font-size: 16px; 
  transition: background-color 0.3s; 
}

.submit-modal-btn:hover {
  background-color: #45a049; 
}

/* Details modal styles */
#details-modal h2 {
  margin-top: 0; 
}

/* Paragraph styles for details */
#details-modal p {
  margin: 5px 0; 
  line-height: 1.6; 
}

/* Strong elements in details */
#details-modal strong {
  display: inline-block; 
  width: 100px; 
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .modal-content {
      margin: 5% auto; /* Adjust margin for smaller screens */
      width: 90%; /* Use more width on smaller screens */
  }
}
